<template>
    <nav>
        <div class="nav-wrapper white">
            
            <div class="container">
                <router-link to="/" class="brand-logo black-text center"><i class="material-icons prefix"> blur_circular</i>CarPal Customer Engine </router-link>
                <ul class="left"> <li v-if="isLoggedIn"><span class="email black-text">Logged in as: <b>{{currentUser}}</b></span></li> </ul>
                <ul class="right">
                    
                    <li v-if="isLoggedIn"><router-link to="/" class="black-text">Dashboard</router-link></li>
                    <li v-if="!isLoggedIn"><router-link to="/login" class="black-text">Login</router-link></li>
                    <li v-if="!isLoggedIn"><router-link to="/register" class="black-text">Register</router-link></li>
                    <li v-if="isLoggedIn"><button v-on:click="logout" class="btn black">Logout</button></li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
import firebase from 'firebase';
export default {
    name: 'navbar',
    data() {
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
        if(firebase.auth().currentUser) {
            this.isLoggedIn = true
            this.currentUser = firebase.auth().currentUser.email
        }
    },
    methods: {
        logout: function() {
            firebase.auth().signOut().then(() => {
                this.$router.go({path: this.$router.path});
            });
        }
    }   
}; 
</script>

<style scoped>
    .email {
        padding-right: 10px;
    }
</style>




